/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  appearance: none;
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Rg.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Rg.woff") format("woff"), url("../assets/fonts/LINESeedKR-Rg.woff2") format("woff2");
  font-weight: 400;
}
@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Th.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Th.woff") format("woff"), url("../assets/fonts/LINESeedKR-Th.woff2") format("woff2");
  font-weight: 300;
}
@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Bd.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Bd.woff") format("woff"), url("../assets/fonts/LINESeedKR-Bd.woff2") format("woff2");
  font-weight: 700;
}
.type-heading {
  position: absolute;
  left: -20px;
  top: 0;
  transform: translateX(-100%);
  font-size: 14px;
  font-weight: 700;
  color: var(--color-black);
  opacity: 0.4;
}
@media all and (max-width: 767px) {
  .type-heading {
    font-size: 13px;
    left: 0;
    top: -10px;
    transform: translateY(-100%);
  }
}
.type-main {
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .type-main {
    font-size: 14px;
  }
}
.type-normal {
  font-size: 13px;
  font-weight: 400;
  color: var(--gray6);
}
@media all and (max-width: 767px) {
  .type-normal {
    font-size: 12px;
  }
}
.type-big {
  font-size: 18px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .type-big {
    font-size: 16px;
  }
}
.type-black {
  color: var(--color-black);
}
.type-white {
  color: var(--color-black);
}

:root {
  --color-black: #1f1f1f;
  --color-white: #fcfcfc;
  --color-gray400: #4f4f4f;
  --color-gray700: #2f2f2f;
  --gray0: #f8f9fa;
  --gray1: #f1f3f5;
  --gray2: #e9ecef;
  --gray3: #dee2e6;
  --gray4: #ced4da;
  --gray5: #adb5bd;
  --gray6: #868e96;
  --gray7: #495057;
  --gray8: #343a40;
  --gray9: #212529;
  --padding: 10px;
  --border-radius: 12px;
  --max-width: 960px;
  --content-width: 880px;
}
@media all and (max-width: 767px) {
  :root {
    --padding: 8px;
    --border-radius: 8px;
  }
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  background-color: var(--color-white);
  border-bottom: 1px solid var(--gray2);
  transform: translateY(-100%);
  transition: transform 0.4s ease-out;
}
.header.on {
  transform: translateY(0);
}
.header .inner {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: calc(var(--padding) * 1.5) var(--padding);
  box-sizing: border-box;
  max-width: var(--content-width);
}
@media all and (max-width: 767px) {
  .header .inner {
    padding: calc(var(--padding) * 1.5) calc(var(--padding) * 2);
  }
}
.header .logo {
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .header .logo {
    font-size: 14px;
  }
}
.header .nav {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.header .nav .item {
  display: inline-block;
}
.header .nav .item + .item {
  margin-left: var(--padding);
}
.header .github {
  display: inline-block;
  vertical-align: middle;
  width: 22px;
  margin-left: var(--padding);
}
.header .github .color {
  fill: var(--gray8);
  transition: fill 0.4s ease-out;
}
.header .github .svg {
  width: 100%;
}
.header .github:hover .color, .header .github:focus .color {
  fill: var(--gray6);
}

.page-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  z-index: 1;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.page-wrap::-webkit-scrollbar {
  display: none;
}
.page-inner {
  width: 100%;
  margin: 0 auto;
  padding: 150px 0;
  box-sizing: border-box;
  max-width: var(--max-width);
}
@media all and (max-width: 767px) {
  .page-inner {
    padding: 40px 0;
  }
}
.page-section {
  margin: 0 auto;
  padding: var(--padding);
  box-sizing: border-box;
  max-width: var(--content-width);
}
@media all and (max-width: 767px) {
  .page-section {
    padding: calc(var(--padding) * 2);
  }
}
.page-section + .page-section {
  margin-top: 150px;
}
@media all and (max-width: 767px) {
  .page-section + .page-section {
    margin-top: 60px;
  }
}
.page-title {
  margin-bottom: 14px;
  color: var(--gray8);
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .page-title {
    font-size: 14px;
  }
}

.section-wrap {
  position: relative;
  margin: 0 auto;
  max-width: var(--content-width);
}
@media all and (max-width: 767px) {
  .section-wrap {
    margin: 0 8vw;
  }
}
.section-inner.inner-padding {
  padding: 30px 45px;
}
.section-title {
  background-color: var(--gray1);
  padding: var(--padding) calc(var(--padding) * 2);
  margin-bottom: var(--padding);
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
  border-radius: var(--border-radius);
}
@media all and (max-width: 767px) {
  .section-title {
    font-size: 14px;
  }
}

.sticky-wrap {
  position: relative;
  width: 100%;
  height: calc(2 * var(--vh));
}
.sticky-inner {
  position: sticky;
  top: 0;
  height: var(--vh);
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}
.loading.off {
  display: none;
}
.loading-inner {
  position: relative;
  width: 100%;
  height: 100%;
}
@media (prefers-color-scheme: dark) {
  .loading-inner {
    background-color: var(--color-bg);
  }
}

html,
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Line Seed";
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--gray6);
}
main {
  background-color: var(--color-white);
}

#p5 {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 110vw;
  height: 110vh;
  z-index: 2;
  overflow: hidden;
  pointer-events: none;
}
@media all and (max-width: 767px) {
  #p5 {
    width: 105vw;
    height: 105vh;
  }
}
#p5 canvas {
  position: relative;
  width: 100%;
  height: 100%;
}

.package {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: var(--vh);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 10;
}
.package-title {
  display: flex;
  width: 100px;
  height: 100px;
  align-items: center;
  justify-content: center;
  z-index: 1;
  background-color: var(--color-white);
}
@media all and (max-width: 767px) {
  .package-title {
    width: 80px;
    height: 80px;
  }
}
.package-title span {
  font-size: 13px;
  color: var(--color-black);
}
@media all and (max-width: 767px) {
  .package-title span {
    font-size: 12px;
  }
}
.package-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
}
.package-bg.hide {
  opacity: 0;
}
.package-guide {
  position: absolute;
  top: 80%;
  font-size: 14px;
  color: var(--color-black);
  opacity: 0.5;
}
@media all and (max-width: 767px) {
  .package-guide {
    font-size: 12px;
  }
}
.package-guide span {
  animation: blink 2.4s infinite;
}

.banner {
  padding: 150px 0;
}
.banner .title {
  display: block;
  margin-bottom: var(--padding);
  font-size: 18px;
  font-weight: 700;
  color: var(--gray8);
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .banner .title {
    font-size: 16px;
  }
}

.project {
  display: flex;
  flex-direction: column;
  padding: calc(var(--padding) * 2);
  background-color: var(--gray1);
  border-radius: 6px;
}
.project + .project {
  margin-top: 30px;
}
@media all and (max-width: 767px) {
  .project + .project {
    margin-top: 24px;
  }
}
.project .intro {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
}
.project .title {
  display: block;
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 767px) {
  .project .title {
    font-size: 14px;
  }
}
.project .desc {
  display: block;
  font-size: 13px;
  font-weight: 400;
  color: var(--gray6);
}
@media all and (max-width: 767px) {
  .project .desc {
    font-size: 12px;
  }
}
@media all and (max-width: 767px) {
  .project .desc {
    display: none;
  }
}
.project .preview-list {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  overflow-x: auto;
  margin-bottom: 24px;
}
.project .preview-list li + li {
  margin-left: calc(var(--padding) * 2);
}
.project .preview-list video,
.project .preview-list img {
  display: block;
  height: auto;
  border: 1px solid var(--gray3);
}
.project .preview-list video.vert,
.project .preview-list img.vert {
  width: 240px;
  border-radius: 12px;
}
@media all and (max-width: 767px) {
  .project .preview-list video.vert,
.project .preview-list img.vert {
    width: 200px;
    border-radius: 10px;
  }
}
.project .preview-list video.hor,
.project .preview-list img.hor {
  width: 560px;
  border-radius: 6px;
}
@media all and (max-width: 767px) {
  .project .preview-list video.hor,
.project .preview-list img.hor {
    width: 400px;
  }
}
.project .preview-list video.hor.height,
.project .preview-list img.hor.height {
  width: auto;
  height: 350px;
}
@media all and (max-width: 767px) {
  .project .preview-list video.hor.height,
.project .preview-list img.hor.height {
    width: auto;
    height: 280px;
  }
}
.project .tag {
  background-color: var(--color-white);
}

.tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding);
  height: 32px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  background-color: var(--gray1);
  color: var(--gray7);
  border-radius: 6px;
  transition: background-color 0.4s ease-out, color 0.4s ease-out;
  font-size: 13px;
  font-weight: 400;
  color: var(--gray6);
}
@media all and (max-width: 767px) {
  .tag {
    font-size: 12px;
  }
}
@media all and (max-width: 767px) {
  .tag {
    font-size: 12px;
  }
}
.tag:hover, .tag:focus {
  background-color: var(--gray3);
  cursor: pointer;
}
.tag-list {
  display: flex;
  flex-wrap: wrap;
}
.tag-list li {
  display: inline-flex;
  margin-right: 6px;
  margin-bottom: 6px;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}